"use client";

import React, { useState, useEffect } from "react";
import {
  colors,
  spacing,
  fontSize,
  fontWeight,
  borderRadius,
  shadow,
  transition,
} from "@/lib/style/styles";
import { faqs as initialFaqs } from "@/lib/data/fqa";

export default function FqaPage() {
  const [faqs, setFaqs] = useState([]);
  const [search, setSearch] = useState("");
  const [openId, setOpenId] = useState(null);

  useEffect(() => {
    const saved = localStorage.getItem("faqs");
    if (saved) {
      setFaqs(JSON.parse(saved));
    } else {
      const withIds = initialFaqs.map((f, i) => ({ ...f, id: i + 1 }));
      localStorage.setItem("faqs", JSON.stringify(withIds));
      setFaqs(withIds);
    }
  }, []);

  const filteredFaqs = faqs.filter((item) =>
    item.question.toLowerCase().includes(search.toLowerCase())
  );

  const toggleFaq = (id) => {
    setOpenId(openId === id ? null : id);
  };

  return (
    <div style={styles.container}>
      <div style={styles.noticeBox}>
        <h2 style={styles.title}>FAQ</h2>
        <p style={styles.subtitle}>
          <em style={{ color: colors.primary }}>MovieHub</em> Ïù¥Ïö© Ïãú Í∂ÅÍ∏àÌïú Ï†êÏùÑ ÌôïÏù∏Ìï† Ïàò ÏûàÏäµÎãàÎã§.
        </p>

        {/* Í≤ÄÏÉâÏ∞Ω */}
        <div style={styles.searchBox}>
          <input
            type="text"
            placeholder="Í≤ÄÏÉâÏñ¥Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            style={styles.searchInput}
          />
          <button style={styles.searchButton}>Í≤ÄÏÉâ</button>
        </div>

        {/* FAQ ÌÖåÏù¥Î∏î */}
        <table style={styles.table}>
          <thead>
            <tr>
              <th style={styles.tableHeader}>Î≤àÌò∏</th>
              <th style={styles.tableHeader}>ÏßàÎ¨∏</th>
            </tr>
          </thead>
          <tbody>
            {filteredFaqs.length === 0 ? (
              <tr>
                <td colSpan="2" style={styles.emptyCell}>
                  Îì±Î°ùÎêú FAQÍ∞Ä ÏóÜÏäµÎãàÎã§.
                </td>
              </tr>
            ) : (
              filteredFaqs.map((faq) => (
                <React.Fragment key={faq.id}>
                  <tr
                    style={{
                      ...styles.tableRow,
                      cursor: "pointer",
                      backgroundColor:
                        openId === faq.id ? colors.darkGray : "transparent",
                    }}
                    onClick={() => toggleFaq(faq.id)}
                  >
                    <td style={styles.tableCell}>{faq.id}</td>
                    <td style={styles.tableCell}>
                      <em style={{ color: colors.primary }}>MovieHub</em>{" "}
                      {faq.question}
                    </td>
                  </tr>

                  {/* ‚úÖ ÌéºÏ≥êÏßà ÎãµÎ≥Ä ÏòÅÏó≠ */}
                  {openId === faq.id && (
                    <tr>
                      <td colSpan="2" style={styles.detailCell}>
                        {faq.answer || "ÎÇ¥Ïö©Ïù¥ ÏóÜÏäµÎãàÎã§."}
                      </td>
                    </tr>
                  )}
                </React.Fragment>
              ))
            )}
          </tbody>
        </table>

        {/* ÌïòÎã® Î≤ÑÌäº */}
        <div style={styles.footerBtns}>
          <button style={styles.footerButton}>Í≤ÄÏÉâÍ≤∞Í≥º ÏàòÏßëÏóê ÎåÄÌïú Ï†ïÏ±Ö</button>
          <button style={styles.footerButton}>MovieHub ÏÇ¨Ïö©Î¨∏Ïùò</button>
          <button style={styles.footerButton}>Ï†úÌú¥Ï†úÏïà</button>
          <button style={styles.footerButton}>Í≥†Í∞ùÏÑºÌÑ∞</button>
        </div>
      </div>
    </div>
  );
}

